import React, { lazy, Suspense, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import LoadingSpinner from './shared/components/LoadingSpinner';
import { path } from './shared/utils/constant';
import ProtectedRoute from './features/auth/components/ProtectedRoute';
import { fetchCurrentUser } from './features/auth/slices/authSlice';

// Lazy load pages
const HomePage = lazy(() => import('./pages/public/HomePage'));
const AuthPage = lazy(() => import('./pages/public/AuthPage'));
const NotFound = lazy(() => import('./pages/public/404'));
const AboutPage = lazy(() => import('./pages/public/AboutPage'));
const ForumPage = lazy(() => import('./pages/public/ForumPage'));
const ProfilePage = lazy(() => import('./pages/protected/ProfilePage'));
const ReadingHistoryPage = lazy(() => import('./pages/protected/ReadingHistoryPage'));
const DashboardPage = lazy(() => import('./pages/protected/DashboardPage'));
const TarotCardsPage = lazy(() => import('./pages/public/TarotCardsPage'));

// Tarot components
const TarotHome = lazy(() => import('./features/tarot/pages/TarotHome'));
const TarotReadingPage = lazy(() => import('./features/tarot/pages/TarotReadingPage'));
const TarotReading = lazy(() => import('./features/tarot/components/TarotReading'));
const TarotDailyPage = lazy(() => import('./pages/public/DailyTarotPage'));

function App() {
  const dispatch = useDispatch();

  useEffect(() => {
    // Kim tra nu c token  fetch thng tin ngi dng hin ti
    const token = localStorage.getItem('token');
    if (token) {
      dispatch(fetchCurrentUser());
    }
  }, [dispatch]);

  return (
    <Router>
      <Suspense fallback={<LoadingSpinner size="large" />}>
        <Routes>
          {/* Public Routes */}
          <Route path={path.PUBLIC.HOMEPAGE} element={<HomePage />} />
          <Route path={path.AUTH.LOGIN} element={<AuthPage />} />
          <Route path={path.AUTH.REGISTER} element={<AuthPage />} />
          <Route path={path.PUBLIC.ABOUTPAGE} element={<AboutPage />} />
          <Route path={path.PUBLIC.FORUMPAGE} element={<ForumPage />} />
          <Route path={path.PUBLIC.TAROTCARDS} element={<TarotCardsPage />} />
          
          {/* Tarot Routes */}
          <Route path="/tarot" element={<TarotHome />} />
          <Route path={path.PUBLIC.TAROTREADINGS} element={<TarotReading />} />
          <Route path="/tarot-readings/:type" element={<TarotReadingPage />} />
          <Route path={path.PUBLIC.DAILYTAROT} element={<TarotDailyPage />} />s
          
          {/* Protected Routes */}
          <Route path={path.PROTECTED.PROFILE} element={
            <ProtectedRoute>
              <ProfilePage />
            </ProtectedRoute>
          } />
          <Route path={path.PROTECTED.READING_HISTORY} element={
            <ProtectedRoute>
              <ReadingHistoryPage />
            </ProtectedRoute>
          } />
          <Route path={path.PROTECTED.DASHBOARD} element={
            <ProtectedRoute>
              <DashboardPage />
            </ProtectedRoute>
          } />
          
          {/* 404 Route */}
          <Route path="*" element={<NotFound />} />
        </Routes>
      </Suspense>
    </Router>
  );
}

export default App;